.button {
    display: flex;
    align-items: center;
    height: 50px;
    font-size: 1.6rem;
    padding: 0 15px;
    border-radius: 5px;
    border: none;
    cursor: pointer;
    transition: all 0.3s;
    color: #000;
}

.button:hover {
    background-color: #fbcb01;
}

@media screen and (max-width: 480px) {
    .button {
        font-size: 1.3rem;
        height: 45px;
        padding: 0 13px;
    }

    .button:hover {
        background-color: #95d330;
    }

    .button:active {
        background-color: #fbcb01;
    }
}