.modalOverlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1;
}

.modalWindow {
    background-color: #141f25;
    margin: 1.5rem;
    padding: 1.5rem;
    border-radius: 5px;
    box-shadow: 0 0 24px #141f25;
    display: flex;
    align-items: center;
    flex-direction: column;
    gap: 1rem;
    outline: none;
}

.codeInput input {
    width: 3rem;
    font-size: 1.5rem;
    font-weight: bold;
    text-align: center;
    padding: 0.5rem 0;
    margin: 0 0.5rem;
    border: none;
    border-radius: 5px;
    text-transform: uppercase;
}

.codeInput input:focus {
    transform: scale(1.1);
    transition: all 0.3s;
}

.codeInput input:not(:focus) {
    transform: scale(1.1);
    transition: all 0.3s;
    background-color: rgb(239, 247, 250, 0.7);
}

.codeInputSuccess {
    color: white;
    font-weight: 600;
    background-color: unset !important;
    outline: none;
    pointer-events: none;
    user-select: none;
}

.verifyAttemptsErrorText {
    color: #df8675;
}

.attemptsText {
    color: #ffd702;
}

.redirectText {
    color: #eff7fab3;
    animation: redirectText 3s 1;
}

@keyframes redirectText {
    from {
        opacity: 0.5;
    }

    to {
        color: #df8675;
        opacity: 1;
    }
}
